.triple-spinner {
  display: block;
  position: absolute;
  margin : auto;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  border: 4px solid transparent;
  border-top: 4px solid #2d1694;
  -webkit-animation: spin 1.7s linear infinite;
  animation: spin 1.7s linear infinite;
  top: 43%;
  left: 44%;
  transform: translate(-50%, -50%);
}

.triple-spinner::before,
.triple-spinner::after {
  content: "";
  position: absolute;
  border-radius: 50%;
  border: 4px solid transparent;
}
.triple-spinner::before {
  content: "";
  position: absolute;
  border-radius: 50%;
  border: 4px solid transparent;
  top: 5px;
  left: 5px;
  right: 5px;
  bottom: 5px;
  border-top-color: #5d50ce;
  -webkit-animation: spin 3s linear infinite;
  animation: spin 3.5s linear infinite;
}
.triple-spinner::after {
  content: "";
  position: absolute;
  border-radius: 50%;
  border: 4px solid transparent;
  top: 15px;
  left: 15px;
  right: 15px;
  bottom: 15px;
  border-top-color: #3abaf5;
  -webkit-animation: spin 1.5s linear infinite;
  animation: spin 1.75s linear infinite;
}

@-webkit-keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
